<template>
  <AppRow permission="WITHDRAW_ADMINISTRATION">
    <v-col cols="12">
      <DatePicker
        label="Даты создания"
        @save="saveDates"
      />
    </v-col>

    <v-col cols="12">
      <v-card
        flat
        class="px-4 pb-2"
      >
        <v-card-title>
          <v-row>
            <v-col
              cols="12"
              sm="6"
            >
              Выводы: 0
            </v-col>
            <v-col class="text-right d-sm-block d-flex justify-center">
              <v-btn-toggle v-model="group">
                <v-btn
                  value="green"
                  color="success"
                  @click="firstPage"
                >
                  <span class="hidden-sm-and-down">Зелёная</span>
                </v-btn>
                <v-btn
                  value="purple"
                  color="purple"
                  @click="firstPage"
                >
                  <span class="hidden-sm-and-down">Фиолетовая</span>
                </v-btn>
                <v-btn
                  value="yellow"
                  color="warning"
                  @click="firstPage"
                >
                  <span class="hidden-sm-and-down">Жёлтая</span>
                </v-btn>
                <v-btn
                  value="red"
                  color="error"
                  @click="firstPage"
                >
                  <span class="hidden-sm-and-down">Красная</span>
                </v-btn>
              </v-btn-toggle>
            </v-col>
          </v-row>
        </v-card-title>
        <v-data-table
          :headers="headers"
          :items="[]"
          :page.sync="page"
          :items-per-page="itemsPerPage"
          disable-sort
          no-data-text="Выводы отсутствуют"
          hide-default-footer
        >
          <template v-slot:top>
            <v-dialog
              v-model="dialog"
              persistent
              max-width="400px"
            >
              <v-card>
                <v-card-title>{{ dialogTitle }}</v-card-title>
                <v-card-text v-if="!accept">
                  <v-text-field
                    v-model="reason"
                    label="Причина"
                  />
                </v-card-text>
                <v-card-actions>
                  <v-spacer />

                  <v-btn
                    text
                    color="primary"
                    @click="dialog = false"
                  >
                    Отмена
                  </v-btn>
                  <v-btn
                    text
                    color="error"
                    @click="dialog = false"
                  >
                    {{ buttonText }}
                  </v-btn>
                </v-card-actions>
              </v-card>
            </v-dialog>
          </template>

          <template v-slot:[`item.registrationAt`]="{ item }">
            {{ item.registrationAt | moment('DD.MM.YYYY, HH:mm') }}
          </template>

          <template v-slot:[`item.createdAt`]="{ item }">
            {{ item.createdAt | moment('DD.MM.YYYY, HH:mm') }}
          </template>

          <template v-slot:[`item.action`]="{ item }">
            <v-btn
              depressed
              class="text-none ma-1"
              large
              height="36"
              @click.stop="denyWithdraw(item)"
            >
              Отклонить
            </v-btn>
            <v-btn
              depressed
              color="primary"
              class="text-none ma-1"
              large
              height="36"
              @click.stop="acceptWithdraw(item)"
            >
              Одобрить
            </v-btn>
          </template>
        </v-data-table>
        <v-divider />

        <div class="text-center pt-3">
          <v-pagination
            v-model="page"
            :total-visible="9"
            :length="Math.ceil(0 / itemsPerPage)"
          />

          <v-select
            v-model="itemsPerPage"
            :items="[5, 10, 15]"
            label="Выводов на странице"
            class="pa-4 pt-5"
            @change="firstPage"
          />
        </div>
      </v-card>
    </v-col>

    <v-col
      cols="12"
      class="text-center text-h5"
    >
      История выплат
    </v-col>

    <v-col>
      <v-card
        :loading="false"
        flat
        class="px-4 pb-2"
      >
        <v-card-title>
          <v-row>
            <v-col
              cols="12"
              sm="6"
            >
              Выводы:
            </v-col>
            <v-col class="text-right d-sm-block d-flex justify-center">
              <v-btn-toggle v-model="group">
                <v-btn
                  value="success"
                  color="success"
                  @click="firstPage"
                >
                  Одобренные
                </v-btn>
                <v-btn
                  value="completed"
                  color="primary"
                  @click="firstPage"
                >
                  Завершённые
                </v-btn>
              </v-btn-toggle>
            </v-col>
          </v-row>
        </v-card-title>
        <v-data-table
          :headers="headersHistory"
          :items="[]"
          :items-per-page="5"
          no-data-text="История отсутствует"
        />
      </v-card>
    </v-col>
  </AppRow>
</template>

<script>
import AppRow from '@/components/AppRow.vue';
import DatePicker from '@/components/DatePicker.vue';

export default {
  components: {
    AppRow,
    DatePicker,
  },
  data() {
    return {
      dateFrom: '',
      dateTo: '',
      group: '',
      headers: [
        { text: 'ID:', value: 'id', class: 'white--text text-subtitle-2' },
        { text: 'Имя:', value: 'username', class: 'white--text text-subtitle-2' },
        { text: 'Баланс:', value: 'balance', class: 'white--text text-subtitle-2' },
        { text: 'Дата регистрации:', value: 'registrationAt', class: 'white--text text-subtitle-2' },
        { text: 'Депозитов:', value: 'deposits', class: 'white--text text-subtitle-2' },
        { text: 'Выводов:', value: 'withdraws', class: 'white--text text-subtitle-2' },
        { text: 'Вывод:', value: 'amount', class: 'white--text text-subtitle-2' },
        { text: 'Статус:', value: 'status', class: 'white--text text-subtitle-2' },
        { text: 'Дата создания:', value: 'createdAt', class: 'white--text text-subtitle-2' },
        { text: '', value: 'action', align: 'center' },
      ],
      page: 1,
      itemsPerPage: 5,
      dialog: false,
      accept: false,
      id: '',
      reason: '',
      headersHistory: [
        { text: 'ID:', value: 'id', class: 'white--text text-subtitle-2' },
        { text: 'Имя:', value: 'username', class: 'white--text text-subtitle-2' },
        { text: 'Сумма:', value: 'amount', class: 'white--text text-subtitle-2' },
        { text: 'Статус:', value: 'status', class: 'white--text text-subtitle-2' },
        { text: 'Дата создания:', value: 'createdAt', class: 'white--text text-subtitle-2' },
      ],
    };
  },
  computed: {
    dateRangeText() {
      return this.dates.join(' ~ ');
    },
    dialogTitle() {
      return this.accept ? 'Одобрить заявку на вывод' : 'Отклонить заявку на вывод';
    },
    buttonText() {
      return this.accept ? 'Одобрить' : 'Отклонить';
    },
  },
  methods: {
    saveDates(dates) {
      [this.dateFrom, this.dateTo] = dates;
    },
    denyWithdraw(item) {
      this.dialog = true;
      this.accept = false;
      this.id = item.id;
    },
    acceptWithdraw(item) {
      this.dialog = true;
      this.accept = true;
      this.id = item.id;
      this.reason = '';
    },
    firstPage() {
      this.page = 1;
    },
  },
};
</script>
